export class ActionBodyExec {
    actionDefinitionId: string;
    /**
     * The entity upon which to execute the action, typically a node ID or similar.
     */
    targetId?: string;
    params?: any;

    constructor(input?: any) {
        if (input) {
            Object.assign(this, input);
        }
    }

}
